section.Welcome {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
    text-align: center;
}

.Welcome-title {
    font-size: 1.5em;
}

.Welcome-logo {
    -webkit-animation: Welcome-logo-spin infinite 20s linear;
            animation: Welcome-logo-spin infinite 20s linear;
    height: 80px;
}

@-webkit-keyframes Welcome-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes Welcome-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


